.income {
  flex: 4;
  margin: 20px;
  padding: 20px;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  width: 100%;
  box-sizing: border-box;
}
.income-text {
  text-align: center;
}
.income-text ul {
  text-decoration: none;
  list-style-type: none;
}
.heading {
  text-align: center;
  margin: 25px 0;
}
.income-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (max-width: 768px) {
  .income-wrapper {
    flex-direction: column;
  }
  .heading {
    text-align: left;
    margin: 13px 0;
    font-size: 20px;
  }
  .income {
    flex: none;
    margin: 0;
    padding: 0;
  }
  .heading {
    text-align: center;
  }
}
